<template>
  <div class="q-pa-md">
    <q-card flat>
      <q-card-section>
        API Url: {{ apiURL }}
      </q-card-section>

      <q-card-section>
        <div v-if="isLoading">
          <q-spinner-dots color="primary" size="50px" />
          <p>Checking connection...</p>
        </div>
        <div v-else>
          <q-banner
            v-if="isConnected"
            class="bg-green text-white"
            icon="check_circle">
          API ist verfügbar</q-banner>
          <q-banner
            v-else
            class="bg-red text-white"
            icon="error" >
          API ist nicht verfügbar: {{  errorMessage }}</q-banner>
        </div>
      </q-card-section>
    </q-card>
  </div>
</template>

<script>
import { ref } from 'vue';
import axios from 'axios';


// Expected error mode:
//
// - network error  (e.g., not internet connection at all, port error, DNS error, SSL protocol error)
// - timeout (e.g., destination IP not responding)
// - API error (not the expected REST API answer)



export default {
  setup() {
    const isConnected = ref(false);
    const errorMessage = ref('');
    const isLoading = ref(true);
    const apiURL = process.env.POK_API_URL + '/room';

    const checkConnection = async () => {
      isLoading.value = true;
      try {
        const response = await axios.get(apiURL, {timeout: 2000}); // Replace with your API endpoint

        if (response.status === 200) {
          isConnected.value = true;
        } else {
          isConnected.value = false;
          errorMessage.value = `Invalid response from API ${response.statusText}`;
          
        }
      } catch (error) {
        // Note: Although we see more details about the error in the java script
        // console, I did not find any way to access them here?
        isConnected.value = false;
        errorMessage.value = `${error.message} - ${error.code}`;
        console.error(error);
      } finally {
        isLoading.value = false;
      }
    };

    checkConnection();

    return {
      isConnected,
      isLoading,
      checkConnection,
      apiURL,
      errorMessage,
    };
  },
};
</script>